interface Props {
	title: string;
	className?: string;
	optionLabel?: any;
	isSearchable?: boolean;
	menuPlacement?: boolean;
	loadOptionsKey: any;
	label?: string;
	isMulti?: boolean;
	placeholder?: string;
	options?: object[];
	optionValue?: any;
	loadOptionsUrl?: string;
	loadOptionsParams?: any;
	filterParams?: () => void;
	customStyles?: boolean;
	isClearable?: boolean;
	disableOptions?: [];
	children?: React.ReactNode;
	SelectComponent?: any;
	isLoading?: boolean;
	onChange?: (option: any) => void;
	reverse?: boolean;
	defaultOption?: any;
	onClearChange?: () => void;
	pageCount?: number;
	onCreateOption?: () => void;
}

interface FormValues {
	field: object;
	form: object;
}

interface Option {
	id: number;
	[key: string]: any;
}

