const AsyncSelect: React.FC<Props & FieldProps<FormValues>> = ({
	disableOptions = [],
	className = '',
	label,
	isMulti,
	loadOptionsKey,
	placeholder,
	options,
	field,
	optionLabel,
	form: { errors, setFieldValue, setFieldTouched, touched },
	isSearchable,
	menuPlacement,
	loadOptionsUrl,
	optionValue,
	isClearable,
	loadOptionsParams = {},
	filterParams = () => {},
	onCreateOption,
	SelectComponent,
	isLoading,
	onChange,
	reverse = false,
	defaultOption = {},
	onClearChange,
	pageCount,
	...rest
}: Props & FieldProps<FormValues>): JSX.Element => {
	const classNames = cx(
		'field-container',
		touched[field.name] && errors[field.name] && 'has-error',
		className
	);
	const { t } = useTranslation();

	const customStyles = {
		indicatorSeparator: (props: any) => ({
			display: 'none'
		})
	};

	return (
		<div className={classNames}>
			<div>
				{label && <div>{label}</div>}
				<AsyncPaginate
					noOptionsMessage={() => t('no_options')}
					loadingMessage={() => `${t('loading')}...`}
					styles={customStyles}
					id={field.name}
					name={field.name}
					debounceTimeout={300}
					onChange={(option: any) => {
						setFieldValue(field.name, option);
						if (typeof onChange === 'function') {
							onChange(option);
						}
						if (typeof onClearChange === 'function' && option === null) {
							onClearChange();
						}
					}}
					onBlur={(): void => setFieldTouched(field.name, true)}
					getValue={(option: Option): any => option[optionValue]}
					getOptionLabel={(option: Option): any =>
						typeof optionLabel === 'function' ? optionLabel(option) : option[optionLabel]
					}
					getOptionValue={(option: Option): any =>
						typeof optionValue === 'function' ? optionValue(option) : option[optionValue]
					}
					value={field.value}
					additional={{ page: 1 }}
					//@ts-ignore
					loadOptions={(search: string, prevOptions: [], { page }: any): any => {
						return load(
							search,
							prevOptions,
							page,
							loadOptionsUrl,
							filterParams,
							loadOptionsParams,
							loadOptionsKey,
							defaultOption,
							reverse,
							pageCount
						);
					}}
					isOptionDisabled={(option: Option): any =>
						disableOptions
							.reduce((prev: Array<any>, curr: Option): Array<any> => [...prev, curr.id], [])
							.includes(option.id)
					}
					{...{
						isMulti,
						options,
						placeholder,
						isSearchable,
						isClearable,
						menuPlacement,
						onCreateOption,
						SelectComponent,
						isLoading
					}}
					{...rest}
				/>
				{touched[field.name] && errors[field.name] && (
					<div className="ant-form-explain">{errors[field.name]}</div>
				)}
			</div>
		</div>
	);
};

export default AsyncSelect;
