import React from 'react';
import { EmojiType } from '../../interfaces';
import isEmpty from 'lodash/isEmpty';
import { IncomingTyping } from '../../contexts/reducers/chatContextReducer';
import linkify from 'linkify-it';
import { ReplyType } from '../../contexts/reducers/chatInputReducer';
import { EmojiData } from 'emoji-mart';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { checkStringContainsHTML } from 'shared/services/utils/checkStringContainsHTML';

export type RenderReacted = {
	reactedUsers: EmojiType[];
	initialReactedMessage: EmojiType[];
	messageId: number | undefined;
	emoji: string;
	toBeDeleted?: boolean;
	custom_uuid: string;
};

export type ReactToEmojiType = {
	emoji: EmojiData;
	reacted_users: EmojiType[];
	renderReacted: ({ reactedUsers, emoji, messageId }: RenderReacted) => void;
	messageId: number | undefined;
	custom_uuid: string;
};

export function useChatHelpers() {
	const { userData } = useAuth();
	const getRepliedToId = (reply: ReplyType) => {
		return reply && reply.id ? reply.id : null;
	};
	const getRepliedTo = (reply: ReplyType | null) => {
		return reply ? reply : null;
	};

	const videoThumbnail =
		'data:image/png;base64,';

	function getVideoThumb() {
		return videoThumbnail;
	}

	const fileThumbnail =
		'data:image/png;base64,';

	function getFileThumb() {
		return fileThumbnail;
	}

	type ResetUploadMeta = {
		handler: (uploadMeta: { [key: string]: string }) => void;
		metaObj: { [key: string]: string };
		uuid: string;
	};

	function resetUploadMeta({ handler, metaObj, uuid }: ResetUploadMeta) {
		const filteredUploadMeta = { ...metaObj };
		delete filteredUploadMeta[uuid];
		handler(filteredUploadMeta);
	}

	type TypingInfo = {
		typingUsers: IncomingTyping[];
		entityId: number;
	};

	const getTypingInfo = ({ typingUsers, entityId }: TypingInfo) => {
		let typingPlurality = '';

		if (!isEmpty(typingUsers)) {
			typingPlurality = typingUsers.length > 1 ? 'are typing...' : 'is typing...';
		}

		const names = typingUsers
			.filter((typing: IncomingTyping) => +typing.entity_id === entityId)
			.map((user) => user.user.first_name)
			.join(', ');

		return `${names} ${typingPlurality}`;
	};

	function insertAtCursor(myField: any, myValue: any) {
		//TODO: typescript type of arguments
		//MOZILLA and others
		let startPos;
		if (myField.selectionStart || myField.selectionStart == '0') {
			startPos = myField.selectionStart;
			const endPos = myField.selectionEnd;

			myField.value =
				myField.value.substring(0, startPos) +
				myValue +
				myField.value.substring(endPos, myField.value.length);
		} else {
			myField.value += myValue;
		}
		return startPos;
	}


	function addLinksToString(text: string) {
		if (checkStringContainsHTML(text)) return text;
		const trimmedMessage = text.trimRight().replace(/(?:\r\n|\r|\n)/g, '<br/>');

		const matches = linkify().match(text);
		if (!matches) return trimmedMessage;

		let results = '';
		let lastIndex = 0;

		matches.forEach((match, i) => {
			results += text.slice(lastIndex, match.index);
			results += `<a target="_blank" class="truncate block text-kblue hover:underline" rel="noreferrer" href="${match.url}">${match.text}</a>`;
			lastIndex = match.lastIndex;
		});

		results += text.slice(lastIndex, text.length);
		// return results;
		const reLinkifiedMessage = results.trimRight().replace(/(?:\r\n|\r|\n)/g, '<br/>');
		return reLinkifiedMessage;
	}

	function urlifyPhone(text: string) {
		const phoneExp = /^[+]?\d{2,}?[(]?\d{2,}[)]?[-\s.]?\d{2,}?[-\s.]?\d{2,}[-\s.]?\d{0,9}$/im;
		return text.replace(phoneExp, function (url, b, c) {
			const url2 = url.replace(/\D/g, '');
			return '<a href="tel:+' + url2 + '">' + url + '</a>';
		});
	}

	const renderTextMessage = (message: string, Component: any) => {
		const text = urlifyPhone(message);
		const linkifiedText = addLinksToString(text);
		return <Component text={linkifiedText} />;
	};

	const reactToEmoji = ({
		emoji,
		reacted_users,
		renderReacted,
		messageId,
		custom_uuid
	}: ReactToEmojiType) => {
		const userId = userData.id;
		let native: any = null;

		const initialReactedMessage = [...reacted_users];

		if ('native' in emoji) native = emoji.native;

		let reactedUsers: EmojiType[];
		//If none has reacted to this message
		if (!reacted_users || !reacted_users.length) {
			//Update EmojiList
			reactedUsers = [{ emoji: native, users: [userId] }];
			renderReacted({ reactedUsers, messageId, emoji: native, initialReactedMessage, custom_uuid });
			return;
		}

		let calcedReactedUsers: EmojiType[];
		const isUserReactingToSameEmoji = reacted_users?.find(
			(emojiObj: EmojiType) => emojiObj.users.includes(userId) && emojiObj.emoji === native
		);
		if (isUserReactingToSameEmoji) {
			//Remove reaction
			// NotificationManager.success("You have already reacted to this emoji!");
			calcedReactedUsers = reacted_users
				.map((emojiObj) => ({ ...emojiObj, users: emojiObj.users.filter((id) => id !== userId) }))
				.filter((emojiObj) => emojiObj.users.length);

			//Update Emoji List
			renderReacted({
				reactedUsers: calcedReactedUsers,
				messageId,
				emoji: native,
				toBeDeleted: true,
				initialReactedMessage,
				custom_uuid
			});
		} else {
			const allUsersReacted = reacted_users.reduce(
				(acc: number[], curr) => [...acc, ...curr.users],
				[]
			);
			const allReactedEmojies = reacted_users.reduce(
				(acc: string[], curr) => [...acc, curr.emoji],
				[]
			);
			// console.log('emoji info', {reacted_users:message.reacted_users,allUsersReacted,allReactedEmojies, native,userId })

			/*
			 * 1. user reacts to already others reacted emoji , but not himself ?
			 * 2. user reacts again to existing reacted emoji but at the same time he has already reacted to another emoji ?
			 * 3. user reacts to emoji which has not been reacted yet by anyone !
			 * 4. user reacts to emoji which has not been reacted yet by anyone but , he has already reacted to another emoji !
			 * */

			// 1. user reacts to already others reacted emoji , but not himself ||500 - list indices must be integers or slices, not str
			if (!allUsersReacted.includes(userId) && allReactedEmojies.includes(native)) {
				reacted_users = reacted_users.map((emojiObj) =>
					emojiObj.emoji === native ? { ...emojiObj, users: [...emojiObj.users, userId] } : emojiObj
				);
			}
			//2. user reacts again to existing reacted emoji but at the same time he has already reacted to another emoji || there is problem . 500 error
			if (allUsersReacted.includes(userId) && allReactedEmojies.includes(native)) {
				reacted_users = reacted_users
					.map((emojiObj) => ({
						...emojiObj,
						users: emojiObj.users.filter((id) => id !== userId)
					}))
					.map((emojiObj) =>
						emojiObj.emoji === native
							? { ...emojiObj, users: [...emojiObj.users, userId] }
							: emojiObj
					)
					.filter((emojiObj) => emojiObj.users.length);
			}
			//3. user reacts to emoji which has not been reacted yet by anyone || no problem
			if (!allUsersReacted.includes(userId) && !allReactedEmojies.includes(native)) {
				reacted_users = [...reacted_users, { emoji: native, users: [userId] }];
			}

			//4. user reacts to emoji which has not been reacted yet by anyone but , he has already reacted to another emoji || no problem
			if (allUsersReacted.includes(userId) && !allReactedEmojies.includes(native)) {
				const allEmojiesWithoutReactedUser = reacted_users
					.map((emojiObj) => ({
						...emojiObj,
						users: emojiObj.users.filter((id) => id !== userId)
					}))
					.filter((emojiObj) => emojiObj.users.length);
				reacted_users = [...allEmojiesWithoutReactedUser, { emoji: native, users: [userId] }];
			}

			//Update Emoji List
			renderReacted({
				reactedUsers: reacted_users,
				messageId,
				emoji: native,
				initialReactedMessage,
				custom_uuid
			});
		}
	};

	return {
		getVideoThumb,
		getFileThumb,
		getRepliedToId,
		getRepliedTo,
		resetUploadMeta,
		getTypingInfo,
		insertAtCursor,
		renderTextMessage,
		reactToEmoji
	};
}
